@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  background-color: #E0E8FF;
  font-family: "Red Hat Display", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
body .desktop {
  display: none;
}
@media (min-width: 71.875em) {
  body .desktop {
    display: block;
  }
}
@media (min-width: 71.875em) {
  body .mobile {
    display: none;
  }
}
body header .bg-img.mobile {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
@media (min-width: 376px) {
  body header .bg-img.mobile {
    transform: scale(5.2);
  }
}
body header .bg-img.desktop {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
@media (min-width: 87.5em) {
  body header .bg-img.desktop {
    transform: scale(2);
  }
}
body .container {
  max-width: 460px;
  width: 90vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .container .card {
  max-width: 100%;
  background-color: white;
  overflow: hidden;
  border-radius: 27px;
  width: 90vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 10px 5px 37px rgba(114, 128, 167, 0.75);
}
body .container .card .hero-img,
body .container .card .hero-img img {
  max-width: 90vw;
}
@media (min-width: 500px) {
  body .container .card .hero-img,
body .container .card .hero-img img {
    transform: scale(1.05);
  }
}
body .container .card .main-text {
  margin-top: 2.5rem;
  margin-bottom: 1.875rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
body .container .card .main-text h1 {
  font-weight: 700;
  font-size: 1.5625rem;
  color: #1F2F56;
  text-align: center;
  margin-bottom: 0.8125rem;
}
@media (min-width: 71.875em) {
  body .container .card .main-text h1 {
    font-size: 1.8125rem;
  }
}
body .container .card .main-text p {
  font-weight: 500;
  font-size: 1.0625rem;
  line-height: 1.549375rem;
  color: #7280A7;
  text-align: center;
  max-width: 80%;
}
body .container .card .plan-card {
  background-color: #F5F7FF;
  display: flex;
  max-width: 85%;
  align-items: center;
  padding: 1.5rem 0.625rem 1.5rem 0;
  border-radius: 11px;
}
@media (max-width: 18.75rem) {
  body .container .card .plan-card {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 20% 2rem 20%;
  }
}
body .container .card .plan-card img {
  width: 3rem;
  height: 3rem;
  margin-left: 1.5625rem;
  margin-right: 1.25rem;
}
body .container .card .plan-card .plan-text {
  margin-right: 0.625rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
body .container .card .plan-card .plan-text h2 {
  font-weight: 900;
  font-size: 1rem;
  text-align: center;
  color: #1F2F56;
}
body .container .card .plan-card .plan-text h3 {
  font-weight: 500;
  font-size: 1rem;
  text-align: center;
  color: #7280A7;
}
body .container .card .plan-card a {
  font-weight: 700;
  font-size: 0.90625rem;
  text-align: center;
  color: #3829E0;
  transition: color 0.25s ease-in-out;
}
body .container .card .plan-card a:hover {
  color: #766cf1;
  transition: color 0.25s ease-in-out;
}
body .container .card .btns {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  align-items: center;
}
body .container .card .btns .payment {
  font-weight: 900;
  font-size: 0.9375rem;
  text-align: center;
  color: white;
  background-color: #3829E0;
  padding: 0.9375rem 3.125rem 0.9375rem 3.125rem;
  border: none;
  border-radius: 0.9375rem;
  margin-bottom: 0.5rem;
  filter: drop-shadow(1px 13px 15px rgba(56, 41, 224, 0.26));
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out;
  transition-property: background-color, shadow;
}
body .container .card .btns .payment:hover {
  background-color: #766cf1;
  filter: drop-shadow(1px 13px 15px rgba(118, 108, 241, 0.26));
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out;
  transition-property: background-color, shadow;
}
body .container .card .btns .cancel {
  font-weight: 900;
  font-size: 0.9375rem;
  text-align: center;
  color: #7280A7;
  background-color: white;
  padding: 0.9375rem 3.125rem 0.9375rem 3.125rem;
  border: none;
  border-radius: 0.9375rem;
  margin-bottom: 0.5rem;
  transition: color 0.25s ease-in-out;
}
body .container .card .btns .cancel:hover {
  color: black;
  transition: color 0.25s ease-in-out;
}
body .container .card .btns .payment,
body .container .card .btns .cancel {
  cursor: pointer;
}
@media (max-width: 18.75rem) {
  body .container .card .btns .payment,
body .container .card .btns .cancel {
    width: 80%;
  }
}